<template>
  <div class="container">
    <br>
    <div class="row">
      <div class="col-xs-12">
        Thirsty Guys
      </div>
    </div>
    <div class="row">
      <div class="col-xs-9">
        <h1>True Green Bubble Tea Boba Singapore</h1>
      </div>
      <div class="col-xs-3 text-xs-right">
        <h4><b>SGD 25.5</b></h4>
      </div>
    </div>
    <hr>
    <br>
    <div class="row" v-for="(custom, idx) in customisation" :key="idx">
      <div class="col-xs-12">
        <ItemCustomiseCategory :customObject='custom'></ItemCustomiseCategory>
        <hr v-if="idx != customisation.length - 1">
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <br>
        <button type="button" class="btn btn-primary btn-lg btn-block">Submit</button>
      </div>
    </div>
  </div>
</template>

<script>

import ItemCustomiseCategory from "@/components/ItemCustomiseCategory";

export default {
  name: "viewItemCustomise",
  data() {
    return {
      customisation: [
        {
          categoryId: 1,
          name: 'Ice level',
          minSelection: 0,
          maxSelection: 0,
          items: [
            {
              name: 'No ice',
              price: 0
            },
            {
              name: 'Normal ice',
              price: 0.2
            },
            {
              name: 'Huge ice',
              price: 0.2
            }
          ]
        }, {
          categoryId: 2,
          name: 'Sugar level',
          minSelection: 1,
          maxSelection: 1,
          items: [
            {
              name: 'No sugar',
              price: 0
            },
            {
              name: 'Normal sugar',
              price: 0
            },
            {
              name: 'Huge sugar',
              price: 0.2
            }
          ]
        }
      ]
    };
  },
  components: {
    ItemCustomiseCategory
  },
}
</script>


<style scoped>
.align-text-bottom{
  vertical-align: bottom !important;
}
</style>


